.tovars {
  padding-bottom: 48px;

  // колонки с карточками товара
  &__grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 24px;

    @media (max-width: 576px) {
      grid-gap: 16px;
    }

    // колонки с карточками товара
    &--3 {
      grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    }
  }

  &__h2 {
    margin-bottom: 0;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;

    @media (max-width: 768px) {
      margin-bottom: 24px;
    }

    @media (max-width: 390px) {
      margin-bottom: 16px;
    }
  }

  &__footer {
    width: 100%;
    text-align: right;
    margin-top: 24px;

    .more-link {
      font-size: 16px;
      font-weight: 400;
    }
  }
}

@media (max-width: 480px) {
  .tovars {
    padding-bottom: 32px;
  }

  .tovars__footer {
    margin-top: 16px;

    .more-link {
      width: 100%;
      height: 48px;
      line-height: 46px;
      border: 1px solid var(--color-line-gray);
      border-radius: 6px;
      padding: 0 24px;
      font-weight: 500;
      text-align: center;
    }
  }
}
